import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useForm } from "react-hook-form";
import { loginByEmailThunk } from "../../../store/thunk/loginByEmailThunk";

// Action
import { loginAction } from "../../../store/slice/loginSlice";

// Components
import { ModalAndWrapper } from "./ModalAndWrapper";
import { InputLogin } from "../../InputLogin";
import { TitleLogin } from "../../TitleLogin";
import { TextLogin } from "../../TextLogin";
import { ButtonLogin } from "../../ButtonLogin";

// Hook-form
import { emailVal, passVal } from "./Validate";

const Login = () => {
  // Selector store
  const { errorLogin } = useSelector((state) => state.login);

  // dispatch
  const dispatch = useDispatch();

  // HOOK USE FORM
  const {
    register,
    formState: { errors },
    handleSubmit,
    setError,
    clearErrors,
  } = useForm({ mode: "onSubmit" });

  // (f) 
  const handlerLogin = (data) => {
    //    store(  )
    dispatch(loginAction.clearErrorHandler());
    //  
    dispatch(loginByEmailThunk(data));
  };

  // (e)    
  useEffect(() => {
    dispatch(loginAction.clearErrorHandler());
  }, [dispatch]);

  // (e)       STORE
  useEffect(() => {
    //       
    const isError = Object.keys(errorLogin).length;

    if (isError) {
      setError(errorLogin.name, { message: errorLogin.message });
    }
  }, [errorLogin, setError]);

  // (e)       STORE
  useEffect(() => {
    //       
    const isError = Object.keys(errorLogin).length;

    if (!isError) {
      clearErrors();
    }
  }, [errorLogin, clearErrors]);
  //
  return (
    <form action="" onSubmit={handleSubmit(handlerLogin)}>
      <ModalAndWrapper>
        <TitleLogin title=" " />
        <InputLogin
          errors={errors}
          register={register}
          val={emailVal}
          name="emailLogin"
          mode="inputBorder"
          label=""
          type="email"
        />
        <TextLogin text=" ?" position="right" />
        <InputLogin
          errors={errors}
          register={register}
          val={passVal}
          name="passwordLogin"
          mode="inputBorder"
          label=""
          type="password"
        />
        <TextLogin
          text="  ?"
          linkedText=" ."
          linkedTo="/registrationone"
        />
        <ButtonLogin mode="login" name="" type="submit" />
      </ModalAndWrapper>
    </form>
  );
};

export { Login };
