import {
    auth
} from '../firebase'

const checkSessionTimeOut = (user) => {
    //   
    //    3600000(1 )   
    const res = new Date().getTime() - user.reloadUserInfo.lastLoginAt;
    const timeOut = 3600000;
    if (res > timeOut) {
        console.log(" .  !");
        auth.signOut();
        localStorage.setItem('isLoggedIn', false)
    } else {
        console.log(
            ` [ ${(
                (timeOut - res) /
                1000
              ).toFixed()} ] .   `
        );
    }
}

export {
    checkSessionTimeOut,
}